export default {
  props: {
    url: {
      type: String,
      required: true,
      default: '',
    },
    comments: {
      type: Array,
      deep: false,
      immediate: false,
      default: () => [],
    },
  },
  data() {
    return {
      loading: false,
      form: {
        body: '',
      },
      errors: {
        body: '',
      },
    }
  },
  methods: {
    async submit(form) {
      this.loading = true
      this.errors = {
        body: '',
      }
      try {
        await this.$axios.$post(this.url, form)
        this.form = { body: '' }
        this.$toast.success('Komentar anda telah terkirim dan akan segera ditinjau.')
      } catch (e) {
        const { errors, message } = this.$errorResponse(e)
        this.errors = {
          ...this.errors,
          ...errors,
        }
        if (message) {
          this.$toast.danger(message)
        }
        if (!Object.keys(errors).length && !message) {
          this.$toast.danger(this.$errorMessage(e))
        }
      }
      this.loading = false
    },
  },
}
