export default {
  name: 'Feedback',
  props: {
    url: {
      type: String,
      required: true,
      default: '',
    },
  },
  data() {
    return {
      loading: false,
      form: {
        understand: '',
        inspiring: '',
        language_style: '',
        content_flow: '',
        message: '',
        sync_with_me: false,
      },
      errors: {
        understand: '',
        inspiring: '',
        language_style: '',
        content_flow: '',
        message: '',
        sync_with_me: '',
      },
    }
  },

  methods: {
    async submit(form) {
      this.loading = true
      this.errors = {
        understand: '',
        inspiring: '',
        language_style: '',
        content_flow: '',
        message: '',
        sync_with_me: '',
      }
      try {
        await this.$axios.$post(this.url, form)
        this.form = { body: '' }
        this.$toast.success('Feedback anda telah terkirim.')
        this.$emit('close')
      } catch (e) {
        const { errors, message } = this.$errorResponse(e)
        this.errors = {
          ...this.errors,
          ...errors,
        }
        if (message) {
          this.$toast.danger(message)
        }
        if (!Object.keys(errors).length && !message) {
          this.$toast.danger(this.$errorMessage(e))
        }
      }
      this.loading = false
    },
  },
}
